<template>
  <div>
    <h2>Work Experience</h2>
    <ul>
      <li v-for="work in works" :key="work.name">
        <div class="project-item">
          <div class="project-info">
            <h3>{{ work.name }}</h3>
            <h5>{{ work.position }}</h5>
            <div class="tags">
              <div class="tag" v-for="item in work.skills" :key="item">{{ item }}</div>
            </div>
          </div>
          <div class="project-info">
            <h4>Description:</h4>
            <ul class="vertical-list">
              <div class="animated" v-for="(desc, index) in work.descript" :key="index" v-html="desc"></div>
              <a class="animated" v-for="(value, key) in work.links" :key="key" :href="value" target="_blank" rel="noopener" style="padding:10px">➥ {{ key }}</a>
            </ul>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import ScrollReveal from 'scrollreveal'

export default {
  name: 'MyWorks',
  data() {
    return {
      isMobile: false,
      windowWidth: window.innerWidth,
      works: [
        {
          "name": "Futurenest",
          "skills": ["Django", "FastAPI", "PostgreSQL", "RAG",  "Next.js", "TypeScript", "OpenAI", "Gemini", "RabbitMQ", "LlamaIndex", "Git", "Docker"],
          "position": "Software Engineer specialized in Backend Development",
          "descript": [
            "Futurenest is a startup recognized by the <b>Microsoft for Startups Accelerator</b> and the <b>NVIDIA Inception Program</b>.",
            "<span class='underline-offset'>Product A: AI Document Management and Generation System (Core developer)</span>",
            "Built a database-driven AI platform from the ground up, enabling automated document regeneration and transforming an enterprise knowledge Q&A system from concept into a production-ready solution.",
            "Built an <b>Agentic AI chatbot using RAG techniques</b> and the LlamaIndex framework to support dynamic multi-agent task planning and execution, overcoming limitations in extensibility and flexibility of previous designs.",
            "Applied <b>prompt engineering and structured templates to automate the generation of ESG</b> and other domain-specific documents, using multi-stage retrieval to minimize LLM hallucinations and improve output factuality.",
            "Spearheaded the PDF image preprocessing workflow optimizations by replacing single-image API calls with batch async requests and custom parallel streaming, <b>achieving 12× faster processing </b>and enabling multimodal input support.",
            "<b>Resolved tight coupling</b> in Microsoft GraphRAG by introducing a “post-generation upload” strategy, improving modularity and eliminating the need to <b>update 56 files per change</b>",
            "<span class='underline-offset'>Product B: Generative AI Chatbot</span>",
            "Built key features and refactored core modules of a generative AI chatbot (Next.js + Django), integrating LINE Messaging API and optimizing for production performance."
          ],
          "links": {"Futurenest": "https://futurenest.ai/"}
        }
      ]
    };
  },
  mounted() {
    ScrollReveal().reveal('.animated', {
      delay: 300,
      duration: 1000,
      origin: 'bottom',
      distance: '20px',
      easing: 'ease-in-out',
      reset: true,
      viewOffset: { top: 100, bottom: 100 }
    });
    this.checkWidth();
    window.addEventListener('resize', this.checkWidth);
  },
  methods: {
    checkWidth() {
      this.isMobile = window.innerWidth <= 1000;
      this.windowWidth = window.innerWidth;
    }
  },
    beforeUnmount() {
        window.removeEventListener('resize', this.checkWidth);
    }
}
</script>


